___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    list-style: none;
    line-height: 2.5;
    font-family: Arial, Helvetica, sans-serif;
}

.container {
    max-width: 1240px;
    margin: 0 auto;
}

.grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    justify-items: center;
    gap: 20px;
}

.grid > li {
    border: 1px solid #208D45;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}

.header {
    background-color: #97ce4c;
    color: #F0F2EB;
    display: flex;
    justify-content: center;
    border: 1px solid #208D45;
}

.search-form {
    margin: 20px 0px;
    display: flex;
    justify-content: end;
}

.search-form > input {
    border: 1px solid #5CAD4A;
    border-radius: 20px;
    text-align: center;

}

.grid-items {
    width: 300px;
    height: 374.5px;
    overflow: hidden;
}

.grid-images {
    transition: 0.5 ease;
    filter: grayscale(100%);
    border-radius: 20px 20px 0px 0px;
}

.grid-images:hover {
    filter: grayscale(0%);
    border-radius: 20px 20px 0px 0px;
}

.text-card {
    display: flex;
    justify-content: center;
    padding: 10px 0px;
    background-color: #97ce4c;
    width: 100%;
    border-radius: 0px 0px 20px 20px;
    color: #F0F2EB;
}

.buttons-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0px;
    gap: 20px;
}

.buttons-container > div {
    font-size: 25px;
    color: #44281d;
}

.buttons-container > button {
    padding: 15px 20px;
    border: none;
    background-color: #97ce4c;
    color: #F0F2EB;
}

.modal-mask {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .5);
    display: flex;
    align-items: center;
    justify-content: center;
}


.modal-container {
    background-color: #97ce4c;
    padding: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 1px solid #208D45;
    color: #F0F2EB;
}

.modal-content {
    display: flex;
    align-items: center;
    gap: 20px;
    font-size: 20px;
    animation: fade-out 0.3s linear;
}

@keyframes fade-out {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.image-round-full {
    border-radius: 50%;
    border: 2px solid #F0F2EB;
}

.modal-exit-button {
    align-self: flex-end;
    border: none;
    width: 30px;
    height: 30px;
    background-color: #5CAD4A;
    color: #F0F2EB;
    cursor: pointer;
}

@media (max-width: 550px) {
    .modal-content {
        display: flex;
        align-items: center;
        gap: 20px;
        flex-direction: column-reverse;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,gBAAgB;IAChB,gBAAgB;IAChB,yCAAyC;AAC7C;;AAEA;IACI,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,2DAA2D;IAC3D,qBAAqB;IACrB,SAAS;AACb;;AAEA;IACI,yBAAyB;IACzB,mBAAmB;IACnB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,yBAAyB;IACzB,cAAc;IACd,aAAa;IACb,uBAAuB;IACvB,yBAAyB;AAC7B;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,oBAAoB;AACxB;;AAEA;IACI,yBAAyB;IACzB,mBAAmB;IACnB,kBAAkB;;AAEtB;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,oBAAoB;IACpB,uBAAuB;IACvB,gCAAgC;AACpC;;AAEA;IACI,qBAAqB;IACrB,gCAAgC;AACpC;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,iBAAiB;IACjB,yBAAyB;IACzB,WAAW;IACX,gCAAgC;IAChC,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,gBAAgB;IAChB,SAAS;AACb;;AAEA;IACI,eAAe;IACf,cAAc;AAClB;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,yBAAyB;IACzB,cAAc;AAClB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,eAAe;IACf,MAAM;IACN,OAAO;IACP,mCAAmC;IACnC,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;;AAGA;IACI,yBAAyB;IACzB,aAAa;IACb,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;IACtB,yBAAyB;IACzB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,eAAe;IACf,+BAA+B;AACnC;;AAEA;IACI;QACI,UAAU;IACd;;IAEA;QACI,UAAU;IACd;AACJ;;AAEA;IACI,kBAAkB;IAClB,yBAAyB;AAC7B;;AAEA;IACI,oBAAoB;IACpB,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,yBAAyB;IACzB,cAAc;IACd,eAAe;AACnB;;AAEA;IACI;QACI,aAAa;QACb,mBAAmB;QACnB,SAAS;QACT,8BAA8B;IAClC;AACJ","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n    list-style: none;\n    line-height: 2.5;\n    font-family: Arial, Helvetica, sans-serif;\n}\n\n.container {\n    max-width: 1240px;\n    margin: 0 auto;\n}\n\n.grid {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));\n    justify-items: center;\n    gap: 20px;\n}\n\n.grid > li {\n    border: 1px solid #208D45;\n    border-radius: 20px;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    cursor: pointer;\n}\n\n.header {\n    background-color: #97ce4c;\n    col